@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}
.password-toogle-icon{
    cursor: pointer;
}
.form-container {
    margin: 100px auto;
    width: 1000px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.2);
    position: relative;
    border-radius: 10px;
    height: 600px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .form-success {
    text-align: center;
    font-size: 24px;
    margin-top: 80px;
    color: rgb(10, 1, 1);
  }

  .form-content-right {
    width: 1000px;
    border-radius: 10px 10px 10px 10px;
    position:relative;
    background: linear-gradient(60deg, rgb(92, 87, 87) 0%, rgb(129, 117, 117) 100%);
  }

  .form {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 90%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }

  .form h1 {
    font-size: 2rem;
    text-align: start;
    width: 80%;
    margin-bottom: 1rem;
    color: #fff;
  }
  .form h4 {
    text-align: start;
    width: 80%;
    margin-bottom: 1rem;
    margin-top: 1rem;
    color: #fff;
  }
  .form-inputs {
    margin-bottom: 0.5rem;
    width: 80%;
  }
  .form-inputs p {
    font-size: 0.8rem;
    margin-top: 0.5rem;
    color: #f00e0e;
  }
.form-input-con{
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    width: 80%;
    color: #fff;
}
.form-input-con a {
    text-decoration: none;
    color: #27cdff;
    font-weight: 600;
  }
.form-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height: 40px;
    width: 100%;
    border: none;
  }
  .form-input::-webkit-input-placeholder {
    color: #595959;
    font-size: 12px;
  }
  .form-input:-ms-input-placeholder {
    color: #595959;
    font-size: 12px;
  }
  .form-input::placeholder {
    color: #595959;
    font-size: 12px;
  }
  .form-input-btn {
    width: 80%;
    height: 50px;
    margin-top: 10px;
    border-radius: 2px;
    background: linear-gradient(
      90deg,
      rgb(23, 92, 133) 0%,
      rgb(89, 240, 243) 100%
    );
    outline: none;
    border: none;
    color: #fff;
    font-size: 1rem;
    cursor: pointer;
  }
  .form-input-btn:hover {
    cursor: pointer;
    background: linear-gradient(
      90deg,
      rgb(39, 143, 255) 0%,
      rgb(12, 99, 250) 100%
    );
    transition: all 0.4s ease-out;
  }
  
  .form-input-login {
    font-size: 0.8rem;
    margin-top: 10px;
    color: #fff;
    width: 80%;
    text-align: center;
  }
  
  .form-input-login a {
    text-decoration: none;
    color: #27cdff;
    font-weight: 600;
  }
  .password-toogle-icon {
      position: absolute;
      right : 6rem;
      top: 21.5rem;
  }
  .email-toogle-icon{
    position: absolute;
    right : 6rem;
    top: 15.5rem;
    color: green;
  }

  @media only screen and (max-width: 700px) {
    /* For mobile: */
    .form-container{width: 350px;}
    .form-content-right{width: 350px;}
    .password-toogle-icon {
        position: absolute;
        right : 2.4rem;
        top: 21.6rem;
    }
    .email-toogle-icon{
      position: absolute;
      right : 2.4rem;
      top: 15.5rem;
    }
  }
